import { FC, useState } from 'react'
import { Stack, styled } from '@mui/material'
import FileIcon from './file'
import FolderIcon from '@mui/icons-material/Folder';
import DriveFileMoveIcon from '@mui/icons-material/DriveFileMove';
import { blue, grey } from '@mui/material/colors';
import AddIcon from '@mui/icons-material/Add';
import { FilesController } from '../api/files.controller';
import { IDir, IFile } from '../models/dir';
import SelectedFileContent from './selected-file-content';

const MainContainer = styled('div')({

})

const NameContainer = styled('div')({
    display: 'flex',
    alignItems: 'center',
    "&:hover": {
        cursor: "pointer"
    }
})

const IconContainer = styled('div')({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: 44,
    aspectRatio: 1,
    color: blue['400'],
    fontSize: 42,
})

interface dirProps {
    dir: IDir
    refreshFiles: () => void,
}

const Dir: FC<dirProps> = ({ dir, refreshFiles }) => {
    const [showFiles, setShowFiles] = useState(false)
    const [hovered, setHovered] = useState(false)
    const [selectedFile, setSelectedFile] = useState<IFile | null>(null)


    const renderDirname = () => {
        return dir.dirname.split('./uploads/')[1]
    }


    const changeShowFiles = () => {
        setShowFiles(!showFiles)
    }


    const handleAddFile = async () => {
        const inputElement = document.createElement('input')
        inputElement.type = 'file'
        inputElement.addEventListener('change', async (event) => {
            const file = (event.target as HTMLInputElement).files?.[0]
            if (file) {
                console.log(file)
                const res = await FilesController.addFile({ file }, renderDirname())
                if (!res.ok) return window.alert(res.message)
                refreshFiles()
                setShowFiles(true)
            }
        })
        inputElement.click()
    }


    return (
        <MainContainer>
            <NameContainer
                onMouseOut={() => setHovered(false)}
                onMouseOver={() => setHovered(true)}
            >
                <IconContainer onClick={changeShowFiles}>
                    {!showFiles ? <FolderIcon color='inherit' fontSize='inherit' /> : <DriveFileMoveIcon color='inherit' fontSize='inherit' />}
                </IconContainer>
                <div onClick={changeShowFiles}>
                    {renderDirname()}
                </div>
                {hovered && <Stack>
                    <IconContainer onClick={handleAddFile}>
                        <AddIcon />
                    </IconContainer>
                </Stack>}
            </NameContainer>
            {showFiles &&
                <Stack
                    paddingLeft={2}
                    rowGap={2}
                    columnGap={2}
                    direction={'row'}
                    flexWrap={'wrap'}
                    color={grey['700']}
                    padding={2}
                >
                    {dir.files.map((singleFile) => <FileIcon
                        file={singleFile}
                        key={singleFile.name}
                        selectedFile={selectedFile}
                        setSelectedFile={setSelectedFile}
                    />)}
                    {dir.files.length === 0 && <div style={{ color: 'grey', fontStyle: 'italic', fontSize: 18 }}>
                        no files
                    </div>}
                </Stack>
            }
            <SelectedFileContent
                selectedFile={selectedFile}
                dirname={dir.dirname}
                opened={showFiles}
            />
        </MainContainer>
    )
}

export default Dir;