import { FC } from 'react'
import { styled } from '@mui/material'
import InsertDriveFileIcon from '@mui/icons-material/InsertDriveFile';
import { IFile } from '../models/dir';


const MainContainer = styled('div')({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    width: 'fit-content',
    textAlign: 'center'
})

interface FileProps {
    file: IFile,
    selectedFile: IFile | null,
    setSelectedFile: (value: IFile) => void,
}

const FileIcon: FC<FileProps> = ({ file, selectedFile, setSelectedFile, }) => {

    return (
        <MainContainer onClick={() => setSelectedFile(file)}>
            <InsertDriveFileIcon />
            <div>
                {file.name}
            </div>
        </MainContainer>
    )
}

export default FileIcon;