import { FC, useState } from 'react'
import { Button, Card, Checkbox, FormControlLabel, Stack, TextField, styled } from '@mui/material'
import { useNavigate } from "react-router-dom";
import { AuthController } from '../api/auth.controller';
import Layout from '../components/layout';

const CheckBoxContainer = styled('div')({
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
})

interface RegosterProps {

}

const Register: FC<RegosterProps> = (props) => {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [firstName, setFirstName] = useState("")
    const [lastName, setLastName] = useState("")
    const [phoneNumber, setPhoneNumber] = useState("")
    const [shirtSize, setShirtSize] = useState(0)
    const [preferredTechnology, setPreferredTechnology] = useState("")
    const [isAdmin, setIsAdmin] = useState(false)
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()


    const handleSubmit = async () => {
        setLoading(true)
        const res = await AuthController.register({
            email,
            pass: password,
            firstName,
            lastName,
            phoneNumber: +phoneNumber,
            shirtSize,
            preferredTechnology,
            role: isAdmin ? 'admin' : 'user',
        })
        const res2 = await AuthController.login({
            email,
            pass: password,
        })
        setLoading(false)
        if (!res2.ok) return window.alert(res.message)

        if (res2.user) {
            AuthController.saveUserInSession(res2.user)
            navigate('/')
        }
    }


    return (
        <Layout>
            <Stack
                direction='row'
                justifyContent={'center'}
                alignItems={'center'}
                height={'100vh'}
            >
                <Card>
                    <Stack
                        spacing={2}
                        padding={2}
                        width={400}
                    >
                        <h1 style={{ textAlign: 'center' }}>
                            Selleo
                        </h1>
                        <TextField
                            label="Email"
                            placeholder='email'
                            type='email'
                            value={email}
                            onChange={e => setEmail(e.target.value)}
                        />
                        <TextField
                            label="Password"
                            placeholder='password'
                            value={password}
                            onChange={e => setPassword(e.target.value)}
                            type='password'
                        />
                        <TextField
                            label="FirstName"
                            placeholder='first name'
                            type='text'
                            value={firstName}
                            onChange={e => setFirstName(e.target.value)}
                        />
                        <TextField
                            label="LastName"
                            placeholder='last name'
                            type='text'
                            value={lastName}
                            onChange={e => setLastName(e.target.value)}
                        />
                        <TextField
                            label="PhoneNumber"
                            placeholder='phone number'
                            type='tel'
                            value={phoneNumber}
                            onChange={e => setPhoneNumber(e.target.value)}
                        />
                        <TextField
                            label="ShirtSize"
                            placeholder='shirt size'
                            type='text'
                            value={shirtSize}
                            onChange={e => setShirtSize(+e.target.value)}
                        />
                        <TextField
                            label="Preferred technology"
                            placeholder='preffered technology'
                            type='text'
                            value={preferredTechnology}
                            onChange={e => setPreferredTechnology(e.target.value)}
                        />

                        <CheckBoxContainer>
                            <FormControlLabel
                                control={<Checkbox checked={isAdmin} onChange={e => setIsAdmin(e.target.checked)} />}
                                label="admin"
                                value={isAdmin}
                            />

                        </CheckBoxContainer>


                        <Button
                            variant='contained'
                            onClick={handleSubmit}
                            disabled={loading}
                        >
                            Register
                        </Button>
                        <Button onClick={() => navigate("/login")}>
                            login
                        </Button>
                    </Stack>
                </Card>
            </Stack>
        </Layout>
    )
}

export default Register;