import axios from "axios"
import {IDir} from "../models/dir"
import {IResponse} from "../models/response"

interface IGetAllFoldersAndFiles extends IResponse {
    dirs: IDir[]
}

const getAllFoldersAndFiles = async (): Promise<IGetAllFoldersAndFiles> => {
    try {
        const response = await axios.get(`/files/find-files`)

        const resoult: IGetAllFoldersAndFiles = {
            ok: true,
            status: response.status,
            message: response.data.message ?? "success",
            dirs: response.data ?? [],
        }

        return resoult
    } catch (error) {
        console.log(error)
        let message = "Something went wrong"
        let status = 400
        if (message === `AxiosError: Network Error`) message = `Network connection lost`

        if (axios.isAxiosError(error) && error.response) {
            if (error.response.status === 404) message = "Item not found!"
            if (error.response.status === 403) message = "Action forbidden!"
            if (error.response.status) status = error.response.status
        }

        return {
            message,
            status,
            ok: false,
            dirs: [],
        }
    }
}

interface ICreateDirResponse extends IResponse {}

const createDir = async (dirname: string): Promise<ICreateDirResponse> => {
    try {
        const response = await axios.post(`/files/create-directory/${dirname}`)

        const resoult: ICreateDirResponse = {
            ok: true,
            status: response.status,
            message: response.data.message ?? "success",
        }

        return resoult
    } catch (error) {
        console.log(error)
        let message = "Something went wrong"
        let status = 400
        if (message === `AxiosError: Network Error`) message = `Network connection lost`

        if (axios.isAxiosError(error) && error.response) {
            if (error.response.status === 409) message = "Dir already exist!"
            if (error.response.status === 404) message = "Item not found!"
            if (error.response.status === 403) message = "Action forbidden!"
            if (error.response.status) status = error.response.status
        }

        return {
            message,
            status,
            ok: false,
        }
    }
}

interface IAddFileResponse extends IResponse {}

export interface AddFileDto {
    file: File
}

const addFile = async (data: AddFileDto, dirname: string): Promise<IAddFileResponse> => {
    axios.defaults.headers.post["Content-Type"] = "multipart/form-data"
    try {
        const response = await axios.post(`/files/${dirname}/add-file`, data)

        const resoult: IAddFileResponse = {
            ok: true,
            status: response.status,
            message: response.data.message ?? "success",
        }

        axios.defaults.headers.post["Content-Type"] = "application/json"
        return resoult
    } catch (error) {
        console.log(error)
        let message = "Something went wrong"
        let status = 400
        if (message === `AxiosError: Network Error`) message = `Network connection lost`

        if (axios.isAxiosError(error) && error.response) {
            if (error.response.status === 409) message = "File already exist!"
            if (error.response.status === 404) message = "Item not found!"
            if (error.response.status === 403) message = "Action forbidden!"
            if (error.response.status) status = error.response.status
        }

        axios.defaults.headers.post["Content-Type"] = "application/json"
        return {
            message,
            status,
            ok: false,
        }
    }
}

interface IGetFileContentResponse extends IResponse {
    content: string,
}

const getFileContent = async (dirname: string, filename: string): Promise<IGetFileContentResponse> => {
    try {
        const response = await axios.get(`/files/${dirname}/display-content/${filename}`)

        const resoult: IGetFileContentResponse = {
            ok: true,
            status: response.status,
            message: response.data.message ?? "success",
            content: response.data ?? "",
        }

        return resoult
    } catch (error) {
        console.log(error)
        let message = "Something went wrong"
        let status = 400
        if (message === `AxiosError: Network Error`) message = `Network connection lost`

        if (axios.isAxiosError(error) && error.response) {
            if (error.response.status === 404) message = "Item not found!"
            if (error.response.status === 403) message = "Action forbidden!"
            if (error.response.status) status = error.response.status
        }

        return {
            message,
            status,
            ok: false,
            content: "",
        }
    }
}

export const FilesController = {
    getAllFoldersAndFiles,
    createDir,
    addFile,
    getFileContent,
}
