import { FC, useCallback, useEffect, useState } from 'react'
import { LinearProgress, styled } from '@mui/material'
import { IFile } from '../models/dir'
import { FilesController } from '../api/files.controller'
import { grey } from '@mui/material/colors'

const MainContainer = styled('div')({
    background: grey['200'],
})

const Name = styled("span")({
    fontSize: 24,
    fontWeight: 600,
})

interface SelectedFileContentProps {
    selectedFile: IFile | null,
    dirname: string,
    opened: boolean,
}

const SelectedFileContent: FC<SelectedFileContentProps> = ({
    selectedFile,
    dirname,
    opened,
}) => {
    const [loading, setLoading] = useState(false)
    const [content, setContent] = useState("")


    const getFileContent = useCallback(
        async () => {
            if (!selectedFile) return
            setLoading(true)
            const res = await FilesController.getFileContent(dirname.split('./uploads/')[1], selectedFile.name)
            setLoading(false)
            if (!res.ok) return window.alert(res.message)
            setContent(JSON.stringify(res.content))
        },
        [dirname, selectedFile]
    )


    useEffect(() => {
        getFileContent()
    }, [getFileContent])


    if(!opened) return <></>

    return (
        <MainContainer>
            <Name>
                {selectedFile?.name}
            </Name>
            {loading && <LinearProgress />}
            <div style={{
                maxHeight: 300,
                overflow: 'auto',
            }}>
                {content}
            </div>
        </MainContainer>
    )
}

export default SelectedFileContent;