import { FC, useCallback, useEffect, useState } from 'react'
import { Button, Stack, styled } from '@mui/material'
import AddIcon from '@mui/icons-material/Add';
import LogoutIcon from '@mui/icons-material/Logout';
import { AuthController } from '../api/auth.controller';
import { FilesController } from '../api/files.controller';
import Dir from '../components/dir';
import { IDir } from '../models/dir';
import Layout from '../components/layout';
import { useNavigate } from "react-router-dom";

const MainContainer = styled('div')({

})

interface HomeProps {

}


const Home: FC<HomeProps> = (props) => {
    const navigate = useNavigate()
    const [dirs, setDirs] = useState<IDir[]>([])


    const getFiles = useCallback(
        async () => {
            const res = await FilesController.getAllFoldersAndFiles()
            if (!res.ok) return window.alert(res.message)
            setDirs(res.dirs)
        },
        []
    )


    const handleLogout = () => {
        const confirmed = window.confirm("Logout?")
        if (!confirmed) return
        AuthController.logout()
        navigate("/login")
    }


    const handleAddDir = async () => {
        const dirname = window.prompt("Type dirname")
        if (!dirname) return
        const res = await FilesController.createDir(dirname)
        if (!res.ok) return window.alert(res.message)
        getFiles()
    }


    useEffect(() => {
        if (!AuthController.checkUserInSession()) {
            navigate('/login')
        }
        else getFiles()
    }, [getFiles, navigate])


    return (
        <Layout>
            <Stack
                padding={1}
                spacing={1}
                direction={'row'}
            >
                <Button
                    variant='contained'
                    startIcon={<AddIcon />}
                    onClick={handleAddDir}
                >
                    New directory
                </Button>
                <Button
                    variant='text'
                    color='error'
                    startIcon={<LogoutIcon />}
                    onClick={handleLogout}
                >
                    Logout
                </Button>
            </Stack>
            <MainContainer>
                {dirs.map((singleDir) => <Dir
                    dir={singleDir}
                    key={singleDir.dirname}
                    refreshFiles={getFiles}
                />)}
            </MainContainer>
        </Layout>
    )
}

export default Home;